<template>
  <div id="login">
    <h1>Log In</h1>
    <p class="subheading">Please log in to continue</p>
    <form @submit.prevent="login">
      <div class="form-group">
        <label for="email">Email</label>
        <input type="text" name="email" v-model="email">
      </div>
      <div class="form-group">
        <label for="password">Password</label>
        <input type="password" name="password" v-model="password">
      </div>
      <div class="form-group">
        <button class="p-button" type="submit">Log In</button>
        <p class="small">Or
          <router-link to="/register">register</router-link>, instead
        </p>
      </div>
    </form>
  </div>
</template>

<script>
import firebase from "firebase";
import { mapMutations } from 'vuex';

export default {
  name: "login",
  data() {
    return {
      email: "",
      password: ""
    };
  },
  methods: {
    ...mapMutations(['CHANGE']),
    login(e) {
      this.CHANGE(this.email)
      firebase
        .auth()
        .signInWithEmailAndPassword(this.email, this.password)
        .then(
          () => {
            alert(`You are logged in as ${this.email}`);
            this.$router.push({
              name: "rooms",
              params: {
                email: this.email
              }
            });
          },
          err => {
            alert(err.message);
          }
        );
      e.preventDefault();
    }
  }
};
</script>

<style lang="scss">
.form-group {
  width: 325px;
}
</style>


