<template>
  <div id="chat">
    <chatheader></chatheader>
    <p>You are logged in as
      <strong>{{email}}</strong> -
      <router-link to="/profile">View Profile</router-link>
      <button class="l-button" type="logout" @click="logout">Log Out</button>
    </p>
    <div id="msg-section" v-chat-scroll="{always: false, smooth: true}">
      <div v-for="message in messages" :key="message.id">
        <div class="message-section">
          <p>
            <span class="username">{{ message.email }}&nbsp;</span>
            <span class="datetime">{{ message.datetime }}</span>
          </p>
          <p class="message">{{ message.message }}</p>
        </div>
      </div>
    </div>
    <div class="input-section">
      <form id="add-message" @submit.prevent="addMessage">
        <div id="chat-group">
          <input
            type="text"
            name="newMessage"
            id="newMessage"
            v-model="newMsg"
            @keyup.enter="addMessage"
            placeholder="Type something and hit Send or press Enter"
          >
          <button class="c-button" type="submit">Send</button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";
import { db } from "@/firebase/init";
import moment from "moment";
import { mapState } from "vuex";
import Chatheader from "../components/Chatheader.vue";

export default {
  name: "customchat",
  data() {
    return {
      messages: [],
      newMsg: null,
      blankEmail: ""
    };
  },
  components: {
    Chatheader
  },
  created() {
    firebase.auth().onAuthStateChanged(function(user) {
      if (user) {
        return;
      } else {
        window.location.pathname = "/login";
      }
    });
    let ref = db.collection(this.$route.params.pathMatch).orderBy("datetime");

    ref.onSnapshot(snapshot => {
      snapshot.docChanges().forEach(change => {
        if (change.type == "added") {
          let doc = change.doc;
          this.messages.push({
            message: doc.data().message,
            email: doc.data().email,
            datetime: moment(doc.data().datetime).format("lll")
          });
        }
      });
    });
  },
  methods: {
    addMessage() {
      if (this.newMsg) {
        db.collection(this.$route.params.pathMatch)
          .add({
            message: this.newMsg,
            email: this.email,
            datetime: Date.now()
          })
          .catch(error => {
            console.log(error);
          });

        this.newMsg = null;
      }
    },
    logout() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.push({
            name: "login"
          });
        })
        .catch(function(error) {
          console.log(error);
          this.email = this.blankEmail;
        });
    }
  },
  computed: mapState(["email", "chatName"])
};
</script>

<style lang="scss"></style>
