<template>
  <div id="chat">
    <h1 id="chat-header">General Chat</h1>
    <p>
      You are logged in as
      <strong>{{email}}</strong> -
      <router-link to="/profile">View Profile</router-link>
      <button class="l-button" type="logout" @click="logout">Log Out</button>
    </p>
    <div id="msg-section" v-chat-scroll="{always: false, smooth: true}">
      <div v-for="message in messages" :key="message.id">
        <div class="message-section">
          <p>
            <span class="username">{{ message.email }} </span>
            <span class="datetime">{{ message.datetime }}</span>
          </p>
          <p class="message">{{ message.message }}</p>
        </div>
      </div>
    </div>
    <div class="input-section">
      <form id="add-message" @submit.prevent="addMessage">
        <div id="chat-group">
          <input
            type="text"
            name="newMessage"
            id="newMessage"
            v-model="newMsg"
            @keyup.enter="addMessage"
            placeholder="Type something and hit Send or press Enter"
          >
          <button class="c-button" type="submit">Send</button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";
import { db } from "@/firebase/init";
import moment from "moment";
import { mapState } from "vuex";

export default {
  name: "chat",
  data() {
    return {
      messages: [],
      newMsg: null,
      blankEmail: ""
    };
  },
  created() {
    let ref = db.collection("messages").orderBy("datetime");

    ref.onSnapshot(snapshot => {
      snapshot.docChanges().forEach(change => {
        if (change.type == "added") {
          let doc = change.doc;
          this.messages.push({
            message: doc.data().message,
            email: doc.data().email,
            datetime: moment(doc.data().datetime).format("lll")
          });
        }
      });
    });

    firebase.auth().onAuthStateChanged(function(user) {
      if (user) {
        return;
      } else {
        window.location.pathname = '/login'
      }
    });
  },
  methods: {
    addMessage() {
      if (this.newMsg) {
        db.collection("messages")
          .add({
            message: this.newMsg,
            email: this.email,
            datetime: Date.now()
          })
          .catch(error => {
            console.log(error);
          });

        this.newMsg = null;
      }
    },
    logout() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.push({
            name: "login"
          });
        })
        .catch(function(error) {
          console.log(error);
          this.email = this.blankEmail;
        });
    }
  },
  computed: mapState(["email"])
};
</script>

<style lang="scss">
// .l-button {
//   margin-bottom: 10px;
// }

#chat {
  display: block;
}

@media screen and (min-width: 700px) {
  #chat {
    max-width: 700px;
    text-align: center;
    margin: 0 auto;
  }
}

#chat-header {
  margin-bottom: 10px;
}

#msg-section {
  height: 300px;
  width: 95%;
  overflow: auto;
  border: 0.2px solid #552e59;
  margin: 0 auto;
}

#add-message {
  width: 100%;
}

#newMessage {
  text-align: left;
  width: 92%;
  margin-bottom: 10px;
}

.message-section {
  text-align: left;
  padding-left: 10px;
  border-bottom: 0.2px solid #e6e6e6;
}

::-webkit-scrollbar {
  background: #f2f2f2;
}

::-webkit-scrollbar-thumb {
  background: #ba9bbe;
}
</style>
