<template>
  <div id="register">
    <h1>Register</h1>
    <p>Please register to continue</p>
    <form>
      <div class="form-group">
        <label for="email">Email</label>
        <input type="text" name="email" v-model="email">
      </div>
      <div class="form-group">
        <label for="password">Password</label>
        <input type="password" name="password" v-model="password">
      </div>
      <div class="form-group">
        <button class="p-button" @click="register" type="submit">Register</button>
        <p class="small">Or
          <router-link to="/login">login</router-link>, instead
        </p>
      </div>
    </form>
  </div>
</template>

<script>
import firebase from "firebase";
import { mapMutations } from "vuex";

export default {
  name: "register",
  data() {
    return {
      email: "",
      password: ""
    };
  },
  methods: {
    ...mapMutations(['CHANGE']),
    register(e) {
      this.CHANGE(this.email)
      firebase
        .auth()
        .createUserWithEmailAndPassword(this.email, this.password)
        .then(
          () => {
            alert(`Account created for ${this.email}`);
            this.$router.push({
              name: "chat",
              params: {
                email: this.email
              }
            });
          },
          err => {
            alert(err.message);
          }
        );
      e.preventDefault();
    }
  }
};
</script>

<style lang="scss">
.form-group {
  width: 325px;
}
</style>


