<template>
  <div class="rooms">
    <h1>Chat Rooms</h1>
    <p>
      You are logged in as <strong>{{email}}</strong> -
      <router-link to="/profile">View Profile</router-link>
      <button class="l-button" type="logout" @click="logout">Log Out</button>
    </p>
    <button class="c-button" id="general-chat-button">
      <router-link to="/chat">General Chat</router-link>
    </button>
    <label for="newRoom">Make a new chat room</label>
    <input
      class="newroom"
      type="text"
      name="newRoom"
      placeholder="Type a name and press enter"
      v-model="chatName"
      @keyup.enter="addRoom"
    >
    <h2>Other Rooms</h2>
    <div class="roomDiv">
      <router-link
        :to="{ path: `/chatrooms/${room.name}`, params: { name: room.name }}"
        v-for="room in rooms"
        :key="room.id"
      >
        <button class="p-button">{{room.name}}</button>
      </router-link>
    </div>
  </div>
</template>

<script>
import firebase from "firebase";
import { db } from "@/firebase/init";
import { mapState, mapMutations } from "vuex";

export default {
  name: "rooms",
  data() {
    return {
      chatName: "",
      rooms: []
    };
  },
  created() {
    let ref = db.collection("rooms");

    ref.onSnapshot(snapshot => {
      snapshot.docChanges().forEach(change => {
        if (change.type == "added") {
          let doc = change.doc;
          this.rooms.push({
            name: doc.data().name
          });
        }
      });
    });
  },
  methods: {
    ...mapMutations(["CHANGECHAT"]),
    addRoom() {
      this.CHANGECHAT(this.chatName);
      if (this.chatName) {
        db.collection("rooms")
          .add({
            name: this.chatName.replace(/\s/g, '')
          })
          .catch(error => {
            console.log(error);
          });

        this.chatName = null;
      }
    },
    logout() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.push({
            name: "login"
          });
        })
        .catch(function(error) {
          console.log(error);
          this.email = this.blankEmail;
        });
    }
  },
  computed: mapState(["email"])
};
</script>

<style lang="scss">
.rooms {
  label {
    margin-top: 30px;
    text-align: center;
  }
}
.newroom {
  width: 90%;
  margin-bottom: 30px;
  max-width: 300px;
}
#general-chat-button {
  margin-top: 30px;
}
.p-button {
  margin-top: 15px;
}
</style>
