<template>
  <div>
    <h1 id="profile-header">Profile</h1>
    <p>User Email: <strong>{{ email }}</strong></p>
    <p><a id="back" @click="$router.go(-1)">Go Back</a></p>
  </div>
</template>

<script>
import { mapState } from 'vuex';

export default {
    name: "profile",
    computed: mapState([
    "email"
  ])
};
</script>

<style>
#profile-header {
  margin-bottom: 10px;
}

#back {
  cursor: pointer;
  text-decoration: underline;
  color: #552e5a;
}
</style>
